import axios from 'axios';

export const fetchTodos = async () => {
    try {
        const response = await axios.get('http://todos.johnmgrubbs.io/api/todos');
        let get_todos = {}
        for (let i = 0; i < response.data.length; i++) {
            get_todos[response.data[i]["id"]] = {
                "id": response.data[i].id,
                "title": response.data[i].title,
            }
        }
        console.log(get_todos)
        return get_todos;
    } catch (error) {
        console.error('Error fetching todos:', error);
        return [];
    }
};

export const deleteTodoAPI = async (todoId) => {
    console.log(todoId)
    try {
        const response = await axios.delete(`http://todos.johnmgrubbs.io/api/todos/delete/${todoId}`);
        return response.status
    } catch (error) {
      console.error('Error deleting todo:', error);
    }
}